import 'jQuery';
const css = require('./master.css');
const $ = require('jQuery');
const moment = require('moment');

export default class Gantt {
    constructor(dataUrl, containerId) {
        this._name = 'Gantt-plugin';
        this._dataUrl = dataUrl;
        this.containerId = containerId;
        this._init();
    }
    _init() {
        $.ajax({
            dataType: 'json',
            url: this._dataUrl,
            context: this
        })
        .done(this._setData)
        .fail(this._dataError);
        this._setVariables();
    }
    _setVariables() {
        // called only once during initialization
        // default values set
        this.dateTimeIds = [
            'datetime-col-0',
            'datetime-col-1',
            'datetime-col-2',
            'datetime-col-3',
            'datetime-col-4',
            'datetime-col-5',
            'datetime-col-6'
        ];
        this._chartingColors = {
            pointer: 0,
            array: [
                '#3366CC', '#DC3912', '#FF9900',
                '#109618', '#990099', '#3B3EAC',
                '#0099C6', '#DD4477', '#66AA00',
                '#B82E2E', '#316395', '#994499',
                '#22AA99', '#AAAA11', '#6633CC',
                '#E67300', '#8B0707', '#329262',
                '#5574A6', '#3B3EAC' ],
            mutateMap: [],
            next(mutateKey) {
                if (!this.mutateMap.includes(mutateKey)) {
                    this.pointer++;
                    this.mutateMap.push(mutateKey);
                }
                return this.array[this.pointer];
            }
        };
        this._setControlVariables();
    }
    _setControlVariables() {
        // should be called several times whenever gantt plugin control
        //  changes
        this.startDate = moment().startOf('week');
        this.rowHeight = 40;
    }
    get chartingColors() {
        return this._chartingColors;
    }
    get name() {
        return this._name;
    }
    get data() {
        return this._data;
    }
    get containerId() {
        return this._containerId;
    }
    get container() {
        return this._container;
    }
    get rowCount() {
        return this._rowCount ? this._rowCount : 0;
    }
    get $rowTemplate() {
        const template = `
            <div id="row-${this.rowCount}-container" class="row"></div>
        `;
        return $.parseHTML(template);
    }
    get $row() {
        return $(`#row-${this.rowCount}-container`);
    }
    get dateTimeIds() {
        return this._dateTimeIds;
    }
    get startDate() {
        return this._startDate;
    }
    get endDate() {
        return this._endDate;
    }
    get rowHeight() {
        return this._rowHeight;
    }
    get $rowTimeTemplate() {
        var startDateClone = this.startDate.clone();
        const template = `
        <div id="row-${this.rowCount}" class="flex-row">
            <div class="flex-item">
                EMPTY
            </div>
            <div id="${this.dateTimeIds[0]}" class="flex-item">
                ${startDateClone.add(0, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[1]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[2]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[3]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[4]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[5]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
            <div id="${this.dateTimeIds[6]}" class="flex-item">
                ${startDateClone.add(1, 'd').format('ll')}
            </div>
        </div>
        `;
        return $.parseHTML(template);
    }
    get $rowDataTemplate() {
        const template = `
        <div id="row-${this.rowCount}" class="flex-row">
            <div id="row-${this.rowCount}-col-0" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-1" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-2" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-3" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-4" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-5" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-6" class="flex-item">-</div>
            <div id="row-${this.rowCount}-col-7" class="flex-item">-</div>
        </div>
        `;
        return $.parseHTML(template);
    }
    get $rowData() {
        return $(`#row-${this.rowCount}`);
    }
    set data(ajaxResponse) {
        this._data = ajaxResponse;
    }
    set containerId(id) {
        this._containerId = id;
        this._container = $('#' + this._containerId);
    }
    set rowCount(x) {
        this._rowCount = x;
    }
    set dateTimeIds(arr) {
        this._dateTimeIds = arr;
    }
    set startDate(momentObj) {
        this._startDate = momentObj;
    }
    set endDate(momentObj) {
        this._endDate = momentObj;
    }
    set rowHeight(height) {
        this._rowHeight = height;
    }
    _setData(response) {
        this.data = response;
        this._render();
    }
    _dataError(response) {
        throw new Error(`There was an error with the response ${response}`);
    }
    _render() {
        this.container.addClass('flex-container');
        this._addTime();
        this.data.forEach(
            (elem, index, arr) => {
                this._addRow(elem);
            }
        );
        this._fillColors();
    }
    _addTime() {
        var $rowTemplate = this.$rowTemplate;
        var startDateClone = this.startDate.clone();
        this.container.append(this.$rowTemplate);
        this.$row.append(this.$rowTimeTemplate);
        this.dateTimeIds.forEach((v, i, arr) => {
            $(`#${v}`).data('moment', startDateClone.add(i > 0 ? 1 : 0, 'd').toJSON());
        });
        this.rowCount++;
    }
    _addRow(rowData) {
        var $rowTemplate = this.$rowTemplate;
        this.container.append(this.$rowTemplate);
        this.$row.append(this.$rowDataTemplate);
        $(`#row-${this.rowCount}-col-0`).html(rowData.desc);
        this.rowCount++;
    }
    _fillColors() {
        var row, col;
        $.each(this.data, (i, dataRow) => {
            $.each(dataRow.bookings, (j, booking) => {
                if (booking.st.trim() !== 'C') return;
                $.each(this.dateTimeIds, (k, dt) => {
                    var colDate = moment($(`#${dt}`).data('moment'));
                    // refer to moment documentation about inclusivity of this method
                    //  http://momentjs.com/docs/#/query/is-between/
                    if (colDate.isBetween(moment(booking.sd), moment(booking.ed), 'day', '[]')) {
                        let chartColor = this.chartingColors.next(booking.id);
                        row = i + 1;
                        col = k + 1;
                        $(`#row-${row}-col-${col}`).css('background-color', chartColor);
                        $(`#row-${row}-col-${col}`).text(booking.id);
                    }
                });
            });
        });
    }
}
module.exports = Gantt;



